import React, { Component } from 'react';

class Headlines extends Component {
	
	showHeadlines = () => {
		if (this.props.show) {
			return "container"
		} else {
			return "hidden"
		}
	}
	
	render() {
		return (
			<div id="news-bar" className={this.showHeadlines()}>
				<h3>EV News Headlines</h3>
				<div className="row">
					<div className="col-sm-4 article-preview">
						<div className="article-thumb article-a"></div>
						<div>
							<span>VW releases video of insane heads-up display in ID.3 electric car</span>
							<p>
								VW has released a series of videos about some of the more interesting features of the ID.3 electric car, including an insane heads-up display.
							</p>
							<a className="btn btn-info" href="https://electrek.co/2020/04/03/vw-heads-up-display-id-3-electric-car/">Read More</a>
							<p>Electrek | 4/3/20</p>
						</div>
					</div>
					<div className="col-sm-4 article-preview">
						<div className="article-thumb article-b"></div>
						<div>
							<span>GM and Honda are co-developing two new electric vehicles due to arrive in 2024</span>
							<p>
								GM and Honda will jointly develop two new electric vehicles slated for 2024, the latest move by the two automakers to deepen their existing partnership.
							</p>
							<a className="btn btn-info" href="https://techcrunch.com/2020/04/03/gm-and-honda-are-co-developing-two-new-electric-vehicles-due-to-arrive-in-2024/">Read More</a>
							<p>TechCrunch | 4/3/20</p>
						</div>
					</div>
					<div className="col-sm-4 article-preview">
						<div className="article-thumb article-c"></div>
						<div>
							<span>Germany Hits Record 9.2% EV Market Share in March, Tesla Model 3 #1</span>
							<p>
								Europe’s largest auto market, Germany, hit another all-time record plug-in electric vehicle market share in March, reaching 9.2%. The Tesla Model 3 was ...
							</p>
							<a className="btn btn-info" href="https://cleantechnica.com/2020/04/04/germany-hits-record-9-2-ev-market-share-in-march-tesla-model-3-1/">Read More</a>
							<p>CleanTechnica | 4/4/20</p>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default Headlines;
