import React, { Component } from 'react';

class Results extends Component {
	
	showMe = () => {
		if (this.props.show) {
			return "Results"
		} else {
			return "Results hidden"
		}
	}
	
	vehicles = [
		{year: 2020, make: 'BMW', model: 'i3', msrp: 44450, tax: 7500, range: 153, kbb: 4.5, body: 'Compact Sedan', drive: 'RWD', type: 'EV', img: 'i3.jpg'},
		{year: 2020, make: 'Chevrolet', model: 'Bolt', msrp: 37495, tax: 1875, range: 259, kbb: 4.7, body: 'Compact Sedan', drive: 'FWD', type: 'EV', img: 'bolt.jpg'},
		{year: 2021, make: 'Ford', model: 'Mustang Mach-E', msrp: 43895, tax: 7500, range: 230, kbb: null, body: 'Hatchback', drive: 'RWD', type: 'EV', img: 'mach-e.jpg'},
		{year: 2020, make: 'Ford', model: 'Fusion PHEV', msrp: 35000, tax: 4609, range: 26, kbb: null, body: 'Sedan', drive: 'FWD', type: 'PHEV', img: 'fusion.jpg'},
		{year: 2020, make: 'Honda', model: 'Clarity', msrp: 33400, tax: 7500, range: 89, kbb: 4.7, body: 'Sedan', drive: 'FWD', type: 'PHEV', img: 'clarity.jpg'},
		{year: 2020, make: 'Hyundai', model: 'Kona', msrp: 37190, tax: 7500, range: 258, kbb: 4.5, body: 'SUV', drive: 'FWD', type: 'EV', img: 'kona.jpg'},
		{year: 2020, make: 'Hyundai', model: 'Ioniq PHEV', msrp: 26500, tax: 4543, range: 29, kbb: 4.4, body: 'Compact Sedan', drive: 'FWD', type: 'PHEV', img: 'ioniq.jpg'},
		{year: 2020, make: 'Hyundai', model: 'Ioniq Electric', msrp: 33045, tax: 7500, range: 170, kbb: 4.4, body: 'Compact Sedan', drive: 'FWD', type: 'EV', img: 'ioniq.jpg'},
		{year: 2020, make: 'Nissan', model: 'Leaf', msrp: 31600, tax: 7500, range: 149, kbb: 4.4, body: 'Compact Sedan', drive: 'FWD', type: 'EV', img: 'leaf.jpg'},
		{year: 2020, make: 'Tesla', model: 'Model 3', msrp: 39990, tax: 0, range: 250, kbb: 4.7, body: 'Sedan', drive: 'RWD', type: 'EV', img: 'model3.jpg'},
		{year: 2020, make: 'Tesla', model: 'Model S', msrp: 79990, tax: 0, range: 390, kbb: 4.9, body: 'Sedan', drive: 'AWD', type: 'EV', img: 'modelS.jpg'},
		{year: 2020, make: 'Tesla', model: 'Model X', msrp: 84990, tax: 0, range: 351, kbb: 4.8, body: 'SUV', drive: 'AWD', type: 'EV', img: 'modelx.jpg'},
		{year: 2020, make: 'Tesla', model: 'Model Y', msrp: 52990, tax: 0, range: 315, kbb: null, body: 'Compact SUV', drive: 'AWD', type: 'EV', img: 'modely.jpg'},
		{year: 2021, make: 'Tesla', model: 'Cybertruck', msrp: 39990, tax: 0, range: 250, kbb: null, body: 'Pickup', drive: 'AWD', type: 'EV', img: 'cybertruck.jpg'},
		{year: 2020, make: 'Toyota', model: 'Prius Prime', msrp: 27750, tax: 4502, range: 25, kbb: 4.7, body: 'Sedan', drive: 'FWD', type: 'PHEV', img: 'priusprime.jpg'},
		{year: 2020, make: 'Kia', model: 'Niro PHEV', msrp: 29545, tax: 4543, range: 26, kbb: 4.5, body: 'SUV', drive: 'FWD', type: 'PHEV', img: 'nirophev.jpg'},
		{year: 2019, make: 'Kia', model: 'Niro EV', msrp: 38500, tax: 7500, range: 239, kbb: null, body: 'SUV', drive: 'FWD', type: 'EV', img: 'niroev.jpg'}
	]
	
	render() {
		return (
			<div className={this.showMe()}>
				<div id="results-container">
					<div className="row">
						<div id="edit-search" className="col-3">
								<div className="form-group">
									<span>Price</span>
									<div className="input-group">
										<label className="margin5">From:</label>
										<div className="input-group-prepend">
											<span className="input-group-text">$</span>
										</div>
										<select type="text" id="min-price" className="form-control"
											onChange={event => this.props.setMinprice(event.target.value)} 
											value={this.props.parameters.minprice}
										>
											<option>0</option>
											<option>20000</option>
											<option>25000</option>
											<option>30000</option>
											<option>35000</option>
											<option>40000</option>
											<option>45000</option>
											<option>50000</option>
											<option>60000</option>
										</select>
									</div>
									<div className="input-group">
										<label className="padding-right">To:</label>
										<div className="input-group-prepend">
											<span className="input-group-text">$</span>
										</div>
										<select type="text" id="max-price" className="form-control"
											onChange={
												event => this.props.setMaxprice(event.target.value)
											}
											value={this.props.parameters.maxprice}
										>
											<option>25000</option>
											<option>30000</option>
											<option>40000</option>
											<option>50000</option>
											<option>60000</option>
											<option>70000</option>
											<option>80000</option>
											<option>100000</option>
										</select>
									</div>
								</div>
								<div className="form-group">
									<div className="input-group">
										<div className="input-group-append">
											<span className="input-group-text">min range</span>
										</div>
										<input id="range"
											onChange={
												event => this.props.setMinrange(event.target.value)
											}
											type="text" className="form-control"
											value={this.props.parameters.minrange}
										/>
										<div className="input-group-append">
											<span className="input-group-text">mi</span>
										</div>
									</div>
								</div>
								<div className="form-group">
									<span>Make</span>
									<select onChange={event => this.props.setMake(event.target.value)} className="form-control" value={this.props.parameters.make}>
										<option>Any Make</option>
										<option>BMW</option>
										<option>Chevrolet</option>
										<option>Ford</option>
										<option>Honda</option>
										<option>Hyundai</option>
										<option>Nissan</option>
										<option>Tesla</option>
										<option>Toyota</option>
									</select>
								</div>								
								<button id="clear" className="btn btn-primary form-control"
								onClick={this.props.resetFilter}>
									Reset Filter
								</button>
						</div>
						<div id="results-group" className="col-7">
							{
								this.vehicles.map((vehicle, index) => {
									//filter make
									if(vehicle.make === this.props.parameters.make || 'Any Make' === this.props.parameters.make){
									//filter min price
									if(vehicle.msrp > this.props.parameters.minprice){
									//filter max price
									if(vehicle.msrp < this.props.parameters.maxprice){
									//filter minrange
									if(vehicle.range > this.props.parameters.minrange){
									return(
										<div className="row car-result" key={index}>
											<div className="car-thumb">
												<img src={'../thumbnails/' + vehicle.img} alt={vehicle.make + vehicle.model} height='130'/>
											</div>
											<div className="col-6">
												<span className="car-name">
													<span className="car-year">
														{vehicle.year}&nbsp;
													</span>
													{vehicle.make}&nbsp;
													<span className="car-model">
														{vehicle.model}
													</span>
												</span>
												<br />
												<span className="car-price">
													Base Price: ${vehicle.msrp}
												</span>
												<br />
												<span className="car-tax">
													Federal Tax Credit: ${vehicle.tax}
												</span>
												<br />
												<span className="car-range">
													All-electric range: {vehicle.range} mi
												</span>
												<br />
												<span className="kbb">
													KBB Expert Rating: {vehicle.kbb}
												</span>
												<br />
												<span className="car-desc">
													{vehicle.body} | {vehicle.drive} | &nbsp;
													<span className={vehicle.type}>
														{vehicle.type}
													</span>
												</span>
											</div>
										</div>
									);
									
									}}}} else {return null}
								})
							}
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default Results;