import React, { Component } from 'react';
import Nav from './components/Nav';
import Splash from './components/Splash';
import Headlines from './components/Headlines';
import Results from './components/Results';



class App extends Component {
	state = {
		show: {
			Splash: true,
			Headlines: true,
			Results: false
		},
		parameters: {
			make: 'Any Make',
			minprice: 0,
			maxprice: 100000,
			minrange: 0,
			maxrange: 500,
		}
		
	}
	
	setMinprice = (nminprice) => {
		var parameters = {...this.state.parameters}
		parameters.minprice = nminprice;
		this.setState({parameters});
	}
	setMaxprice = (nmaxprice) => {
		var parameters = {...this.state.parameters}
		parameters.maxprice = nmaxprice;
		this.setState({parameters});
	}
	setMinrange = (nminrange) => {
		var parameters = {...this.state.parameters}
		parameters.minrange = nminrange;
		this.setState({parameters});
	}
	setMake = (make) => {
		var parameters = {...this.state.parameters}
		parameters.make = make;
		this.setState({parameters});
	}
	
	showResults = () => {
		this.setState({  show : {Splash: false, Headlines: false, Results: true }  });
	}
	
	showSplash = () => {
		this.setState({  show : {Splash: true, Headlines: true, Results: false }  });
	}
	
	resetFilter = () => {
		var parameters = {...this.state.parameters}
		parameters.make='Any Make';
		parameters.minprice=0;
		parameters.maxprice=100000;
		parameters.minrange=0;
		parameters.maxrange=500;
		this.setState({parameters});
	}
	
	render() {
		return (
			<div className="App">
				<Nav 
					showSplash={this.showSplash}
					resetFilter={this.resetFilter}
				/>
				<Splash 
					setMake={this.setMake}
					show={this.state.show.Splash}
					showResults={this.showResults}
					parameters={this.state.parameters}
				/>
				<Headlines 
					show={this.state.show.Headlines}
				/>
				<Results 
					show={this.state.show.Results}
					parameters={this.state.parameters}
					setMake={this.setMake}
					setMinprice={this.setMinprice}
					setMaxprice={this.setMaxprice}
					setMinrange={this.setMinrange}
					resetFilter={this.resetFilter}
				/>
			</div>
		);
	}
}

export default App;
