import React, { Component } from 'react';

class Splash extends Component {

	state = {
		makes: ['Any Make', 'BMW', 'Chevrolet', 'Ford','Honda', 'Hyundai', 'Nissan', 'Tesla', 'Toyota']
	};
	
	showMe = () => {
		if (this.props.show) {
			return "Splash"
		} else {
			return "Splash hidden"
		}
	}
	render() {
		return (
			<div className={this.showMe()}>
				<div className="row row-spacer"></div>
				<div className="row">
					<div className="col-sm-3"></div>
					<div className="splash-text center-text col-sm-6">
						<p>
							Search and compare the most popular <span className="bold italic">EV</span> and <span className="bold italic">PHEV</span> models on the market!
						</p>
					</div>
					<div className="col-sm-3"></div>
				</div>
				<div id="search" className="row">
					<div className="col-md-3"></div>
					<div className="col-md-4">
						<select id="make" 
						onChange={
							event => this.props.setMake(event.target.value)
						} 
						className="form-control" value={this.props.parameters.make}>
						{this.state.makes.map((value,index) => {
							return <option key={index}>{value}</option>
						})}
						</select>
					</div>
					<div className="col-md-2">
						<button onClick={this.props.showResults} className="btn btn-primary form-control">Compare!</button>
					</div>
				</div>
			</div>
		);
	}
}

export default Splash;
